.shippingContainer {
    width: 100vw;   
    max-width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: #fff;
   

}

.shippingBox {
    background-color: #fff;
    width: 60vw;
    height: 80vh;
    box-sizing: border-box;
    overflow: hidden;

}

.shippingHeading{
    text-align: center;
    font: 400 3vmax "Roboto";
    text-decoration: underline;
    color: rgba(0, 0, 0, 0.687);

}
.shippingForm {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    margin: auto;
    padding: 2vmax;
    height: 70%;
    justify-content: space-evenly;
    transition: all 0.5s;

}
.shippingForm>div {
    display: flex;
    width: 25vw;
    align-items: center;
}

.shippingForm>div>input,
.shippingForm>div>select
 {
    margin:0.5vmax 0;
    padding: 1vmax 4vmax;
    padding-right: 1vmax;
    width: 100%;
    box-sizing: border-box;
    border: 1px solid rgba(0, 0, 0, 0.267);
    border-radius: 4px;
    font: 300 0.9vmax cursive;
    outline: none;

}
.shippingForm>div>select>option{
    font: 400 1.2vmax "Roboto";
}

.shippingForm>div>svg {
    position: absolute;
    transform: translateX(1vmax);
    font-size: 1.6vmax;
    color: rgba(0, 0, 0, 0.687);
}
.shippingBtn{
    border: none;
    background-color: tomato;
    color: #fff;
    font: 300 0.9vmax "Roboto";
    width: 100%;
    padding: 0.8vmax;
    cursor: pointer;
    transition: all 0.5s;
    border-radius: 4px;
    outline: none;
    box-shadow: 0 2px 5px rgba(0, 0, 0, 0.219);
}


.shippingBtn:hover {
    background-color: rgb(179, 66, 46);

}
@media screen and (max-width:600px) {
 .shippingContainer{
    background-color: #fff;
 }   

 .shippingBox{
    width: 100vw;
    height: 95vh;

 }
.shippingForm{
   
    padding: 5vmax;
 }
 .shippingForm>div{
    width: 100vw;
 }
 .shippingForm>div>input,
.shippingForm>div>select
{
    padding: 2.5vmax 7vmax;
    font:300 2vmax cursive;
 }
 .shippingForm>div>select>option{
    font: 400 1.8vmax "Roboto";

}
 .shippingForm>div>svg{
    font-size:2.8vmax;
    padding: 0 1.5vmax;
 }
 .shippingBtn{
    font: 300 2vmax "Roboto";
    padding: 2vmax;
 }
.shippingHeading{
    font: 400 5vmax "Roboto";

}
}
